body {
  background-color: rgb(236, 118, 8);
  font-family: "Roboto";
}
body,
html {
  height: 100%;
}

#root {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  box-sizing: border-box;
  padding: 20px 10px;
  flex-basis: 100%;
  background-color: #fff;
  max-width: 700px;
  border-radius: 5px;
}
.main h2 {
  margin: 10px 0 20px 0;
  text-align: center;
}
.question {
  border: 1px solid #ccc;
  padding: 5px 10px;
  margin: 10px 0;
}
.quest-sec {
  position: relative;
}
.quest-sec i {
  position: absolute;
  top: 50%;
  right: 5px;
  color: rgb(236, 118, 8);
  transform: translateY(-50%);
}
.quest-sec h3 {
  padding: 0;
  margin: 8px 0;
  letter-spacing: 1px;
  font-weight: normal;
}
.question p {
  color: rgb(114, 114, 114);
  font-size: 16px;
}
footer {
  text-align: center;
  color: black;
}
footer i {
  color: red;
}
